import { Error } from '@popup/shared/components/errors/Error';
import React from 'react';
import styles from './PaymentScreenContent.module.css';
import { usePaymentScreen } from '../../hooks/usePaymentScreen';
import { Loader } from '../../../shared/Loader';
import { Button } from '@popup/shared/components/Button';
import { PaymentMethodItem } from './PaymentMethodItem';
import { useNavigate } from 'react-router-dom';

export const PaymentScreenContent: React.FC = () => {
  const {
    loading,
    error,
    selectedPaymentMethod,
    availablePaymentMethods,
    setSelectedPaymentMethod,
    publicOfferUrl,
    onPaymentClick,
  } = usePaymentScreen();

  const navigate = useNavigate();

  const handleBack = () => {
    navigate(-1);
  };

  if (loading) {
    return (
      <Loader className={styles.loader}>   </Loader>
    );
  }

  if (error) {
    return <Error />;
  }

  return (
    <div className={styles.wrapper}>
      <div className={styles.container}>
        <h1 className={styles.title}>  </h1>
        <p className={styles.guarantee}>
           ,  -  
        </p>
        <div className={styles.methods}>
          {availablePaymentMethods.map((method) => (
            <PaymentMethodItem
              key={method}
              active={selectedPaymentMethod === method}
              method={method}
              onClick={setSelectedPaymentMethod}
            />
          ))}
        </div>
        {publicOfferUrl && (
          <p className={styles.agreement}>
             ,  
            <br /> {' '}
            <a className={styles.link} href={publicOfferUrl} target="_blank">
               
            </a>
          </p>
        )}
        <p className={styles.instructions}>
                  
          .
        </p>
        <div className={styles.buttons}>
          <Button
            variant="primary"
            size="default"
            className={styles.button}
            onClick={onPaymentClick}
          >
            
          </Button>
          <Button
            variant="secondary"
            size="default"
            className={styles.button}
            onClick={handleBack}
            data-close="close-button"
          >
            
          </Button>
        </div>
      </div>
    </div>
  );
};
