import { Button } from '@popup/shared/components/Button';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { forwardRef, useCallback } from 'react';
import { StepProps } from '../../types';
import { VrubelIcon } from '../VrubelIcon';

import styles from './WelcomeStep.module.css';
import { useTranslation } from 'react-i18next';

export const WelcomeStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation();

    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleTrial = useCallback(() => {
      handleClick(false, false, true);
    }, [handleClick]);

    const handleLogIn = useCallback(() => {
      onStepChange(1);
    }, [onStepChange]);

    return (
      <div className={styles.wrapper} ref={ref}>
        <div className={styles.sign}>
          <VrubelIcon />
        </div>
        <div className={styles.info}>
          <h1 className={styles.title}>
            <span>{t('title', { ns: 'vrubel' })}</span>
            <br />
            <span> 1   !</span>
          </h1>
          <p className={styles.description}>
             -     <strong>  YouTube</strong>,
               .
          </p>
          <br />
          <p className={styles.description}>
            4-,        1  
            !
            <br />
            <strong>    .</strong>
          </p>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles['primary-button']}
            onClick={handleTrial}
            loading={loading}
            disabled={loading}
          >
             24  
          </Button>
          <Button
            className={styles['secondary-button']}
            variant="link"
            onClick={handleLogIn}
          >
               ID  
          </Button>
        </div>
      </div>
    );
  },
);

WelcomeStep.displayName = 'WelcomeStep';
