import { useEffect, useState } from 'react';
import { PaymentMethods } from '../types/PaymentMethods';
import { PaymentMethod } from '@app/storage/session/shared';

export function usePaymentMethods() {
  const [paymentMethods, setPaymentMethods] = useState<PaymentMethods>([]);

  useEffect(() => {
    const getMethodsMockData = async () => {
      const data = {
        availablePaymentMethods: [PaymentMethod.Card, PaymentMethod.Sbp],
      };

      if (!data.availablePaymentMethods) {
        return;
      }
      setPaymentMethods(data.availablePaymentMethods);
    };
    getMethodsMockData();
  }, []);

  return paymentMethods;
}
