import {
  CookieKey,
  removeCookieData,
} from '@app/storage/cookies/shared';
import {
  getLocalStoragePremiumData,
  getSubscriptionInfo,
  LocalStoragePremiumKey,
} from '@app/storage/local/premium';

import { useEffect } from 'react';

export function useCorrectUserId() {
  useEffect(() => {
    const clearAllLocalAndCookieStorage = async (): Promise<void> => {
      await chrome.storage.local.clear();
      await chrome.storage.session.clear();
      await chrome.storage.sync.clear();
      await chrome.storage.managed.clear();
      await chrome.alarms.clearAll();

      await removeCookieData(CookieKey.UserId);

      chrome.runtime.reload();
    };

    async function checkUserIdFromStorage() {
      try {
        const { userId } = await getLocalStoragePremiumData(
          LocalStoragePremiumKey.UserId,
        );

        if (userId) {
          await getSubscriptionInfo();
        }
      } catch (error) {
        if (error instanceof Error && error.message === 'userNotFound') {
          await clearAllLocalAndCookieStorage();
        }
      }
    }

    checkUserIdFromStorage();
  }, []);
}
