import { RoutePath } from '@popup/project/vrubel/routes/types/RoutePath';
import { Button } from '@popup/shared/components/Button';
import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useTariffs } from '../../hooks/useTariffs';
import { TariffItem } from './TariffItem';
import { Error } from '@popup/shared/components/errors/Error';
import { Loader } from '../../../shared/Loader';

import styles from './TariffsScreenContent.module.css';

export const TariffsScreenContent: React.FC = () => {
  const {
    isTariffError,
    isTariffsLoading,
    tariffs,
    selectedTariff,
    setSelectedTariff,
  } = useTariffs();

  const navigate = useNavigate();

  const handlePaymentClick = () => {
    navigate(RoutePath.Payment, {
      state: { selectedTariff: selectedTariff?.tariff },
    });
  };

  if (isTariffsLoading) {
    return <Loader className={styles.loader} />;
  }

  if (isTariffError) {
    return <Error />;
  }

  if (!tariffs) {
    return null;
  }

  return (
    <div className={styles.wrapper}>
      <div className={styles.container}>
        <h1 className={styles.title}>
           
          <br />
          <span> 1   </span>
        </h1>
        <div className={styles.tariffs}>
          {tariffs.map((tariff) => (
            <TariffItem
              key={tariff.tariff}
              tariff={tariff}
              onClick={setSelectedTariff}
              selected={selectedTariff?.tariff === tariff.tariff}
            />
          ))}
        </div>
        <div className={styles.buttons}>
          <Button
            variant="primary"
            size="default"
            className={styles.button}
            onClick={handlePaymentClick}
          >
            
          </Button>

          <Link to={RoutePath.Root} className={styles.button}>
            <Button
              variant="secondary"
              size="default"
              className={styles.button}
              data-close="close-button"
            >
              
            </Button>
          </Link>
        </div>
      </div>
    </div>
  );
};
