import { Button } from '@popup/shared/components/Button';
import clsx from 'clsx';
import { useEffect } from 'react';
import { useEnterIdForm } from '../hooks/useEnterIdForm';

import styles from './EnterIdForm.module.css';

type EnterIdFormProps = {
  onIdSettled?: () => void;
};

export function EnterIdForm({ onIdSettled }: EnterIdFormProps): JSX.Element {
  const {
    enteredId,
    handleChange,
    handleSubmit,
    inputRef,
    isError,
    isLoading,
    clearError,
    isIdSettled,
  } = useEnterIdForm();

  useEffect(() => {
    if (isIdSettled && onIdSettled) {
      onIdSettled();
    }
  }, [isIdSettled, onIdSettled]);

  return (
    <form onSubmit={handleSubmit} className={styles.form}>
      <div className={styles.content}>
        <div className={styles['input-container']}>
          <input
            ref={inputRef}
            className={clsx(styles.input, {
              [styles['input-error']]: isError,
              [styles['input-with-value']]: enteredId.length > 0,
            })}
            onChange={handleChange}
            value={enteredId}
            placeholder=" ID"
            onFocus={clearError}
          />
          {isError && (
            <span className={styles.error}>
              ,    ID.   .
            </span>
          )}
        </div>
      </div>
      <div className={styles.buttons}>
        <Button
          className={styles.submit}
          type="submit"
          loaderVariant="circle"
          loaderClassName={styles.loader}
          loading={isLoading}
        >
          {isLoading ? '  ID...' : ''}
        </Button>
      </div>
    </form>
  );
}
