import { EnterIdForm } from '@popup/shared/components/EnterIdForm';
import { forwardRef, useCallback } from 'react';
import { StepProps } from '../../types';
import { Button } from '@popup/shared/components/Button';
import styles from './EnterIdStep.module.css';

export const EnterIdStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const updateOnboardingScreenStatus = useCallback(async () => {
      onStepChange(3);
    }, [onStepChange]);

    return (
      <div ref={ref} className={styles.wrapper}>
        <h1 className={styles.title}> ID,</h1>
        <p className={styles.description}>    </p>
        <EnterIdForm onIdSettled={updateOnboardingScreenStatus} />
        <Button
          className={styles.button}
          variant="secondary"
          onClick={() => onStepChange(0)}
        >
           
        </Button>
      </div>
    );
  },
);

EnterIdStep.displayName = 'EnterIdStep';
