import { setLocalStorageData } from '@app/storage/local/shared';
import { Button } from '@popup/shared/components/Button';
import { useNegativeFeedbackUrl } from '@popup/shared/components/Feedback/hooks/useNegativeFeedbackUrl';
import { useFeedbackOverlay } from '@popup/shared/contexts/FeedbackOverlayContext';
import { clsx } from 'clsx';
import { useState } from 'react';
import { Header } from '../shared/Header';
import { NegativeIcon } from './components/NegativeIcon';
import { PositiveIcon } from './components/PositiveIcon';
import { VrubelIcon } from './components/VrubelIcon';

import styles from './Feedback.module.css';

async function setFeedbackProvided(): Promise<void> {
  await setLocalStorageData({ feedbackProvided: true });
}

export function Feedback(): JSX.Element {
  const { isFeedbackOverlayShown, closeFeedbackOverlay } = useFeedbackOverlay();
  const { negativeFeedbackUrl } = useNegativeFeedbackUrl();

  const [isPositive, setIsPositive] = useState(false);
  const [isNegative, setIsNegative] = useState(false);

  function handleClickPositiveFeedback(): void {
    setIsPositive(true);
    void setFeedbackProvided();
  }

  function handleClickNegativeFeedback(): void {
    setIsNegative(true);
    void setFeedbackProvided();
  }

  function handleClickOnBack(): void {
    setIsPositive(false);
    setIsNegative(false);
  }

  function renderFeedbackContent(): JSX.Element {
    if (isPositive) {
      return (
        <>
          <Header />
          <PositiveIcon className={styles['positive-icon']} />
          <div className={styles.info}>
            <h3 className={styles.title}> !</h3>
            <span className={styles.text}>  .</span>
            <br />
            <br />
            <span className={styles.text}>
                     , 
                   
            </span>
          </div>
          <div className={styles.buttons}>
            <a
              rel="noreferrer"
              href={process.env.POPUP_HTML_PUBLIC_FEEDBACK_URL}
              target="_blank"
              className={styles.link}
            >
              <Button
                variant="primary"
                onClick={closeFeedbackOverlay}
                className={styles.button}
              >
                 
              </Button>
            </a>
            <Button
              variant="primary-n"
              className={styles.button}
              onClick={handleClickOnBack}
            >
              
            </Button>
          </div>
        </>
      );
    }

    if (isNegative) {
      return (
        <>
          <Header />
          <NegativeIcon className={styles['negative-icon']} />
          <div className={styles.info}>
            <h3 className={styles.title}>  </h3>
            <span className={styles.text}>
                ,    .
            </span>
            <br />
            <br />
            <span className={styles.text}>
                       
               
            </span>
          </div>
          <div className={styles.buttons}>
            <a
              rel="noreferrer"
              href={negativeFeedbackUrl}
              target="_blank"
              className={styles.link}
            >
              <Button
                onClick={closeFeedbackOverlay}
                variant="primary"
                className={styles.button}
              >
                
              </Button>
            </a>
            <Button
              variant="primary-n"
              className={styles.button}
              onClick={handleClickOnBack}
            >
              
            </Button>
          </div>
        </>
      );
    }

    return (
      <>
        <Header />
        <VrubelIcon className={styles.icon} />
        <div className={styles.info}>
          <h3 className={styles.title}>  ?</h3>
        </div>
        <div className={styles.buttons}>
          <Button
            onClick={handleClickNegativeFeedback}
            variant="secondary"
            className={styles.button}
          >
             
          </Button>
          <Button
            onClick={handleClickPositiveFeedback}
            variant="primary"
            className={styles.button}
          >
             
          </Button>
          <Button
            variant="link"
            className={styles.button}
            onClick={closeFeedbackOverlay}
          >
            
          </Button>
        </div>
      </>
    );
  }

  return (
    <div
      className={clsx(styles.overlay, {
        [styles['overlay-visible']]: isFeedbackOverlayShown,
      })}
    >
      <div className={styles['overlay-content']}>{renderFeedbackContent()}</div>
    </div>
  );
}
