import { OnboardingStep } from '@popup/project/vrubel/components/Onboarding/ui/constants';
import { LimitedOnboardingStatus } from '@popup/project/vrubel/components/Onboarding/ui/types';
import { useCorrectUserId } from '@popup/project/vrubel/hooks/useCorrectUserId';
import { useNavigateToTariffs } from '@popup/project/vrubel/hooks/useNavigateToTariffs';
import { ConnectButton } from '@popup/shared/components/ConnectButton';
import { DebugButton } from '@popup/shared/components/DebugButton';
import { useTheme } from '@popup/shared/contexts/Theme';
import { useOnboardingScreen } from '@popup/shared/hooks/useOnboardingScreen';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

import { Feedback } from '../../../components/Feedback';
import { Onboarding } from '../../../components/Onboarding/ui/Onboarding';
import { PremiumData } from '../../../components/PremiumData';
import { useChangeDevice } from '../../../hooks/useChangeDevice';
import { RoutePath } from '../../types/RoutePath';

import styles from './Root.module.css';

export function Root(): JSX.Element | null {
  const { hasPremium } = usePremiumData();
  const { onboardingStatus } = useOnboardingScreen();

  useTheme();
  useChangeDevice();
  usePremiumAnimation({ isFreeUser: !hasPremium });
  useNavigateToTariffs();
  useCorrectUserId();

  if (onboardingStatus !== OnboardingStep.Done) {
    return (
      <Onboarding
        onboardingStatus={onboardingStatus as LimitedOnboardingStatus}
      />
    );
  }

  return (
    <div className={styles.wrapper}>
      <ConnectButton
        className={styles['connect-button-container']}
        onExpiredRoute={RoutePath.Tariffs}
      />
      <PremiumData />
      <DebugButton className={styles['debug-button']} />
      <Feedback />
    </div>
  );
}
