import { useCancelPremium } from '@popup/shared/hooks/useCancelPremium';
import { RoutePath } from '../../types/RoutePath';
import { Link } from 'react-router-dom';
import { Error } from '@popup/shared/components/errors/Error';
import { Loader } from '@popup/project/vrubel/components/shared/Loader/ui/Loader';
import { Button } from '@popup/shared/components/Button';
import { QuestionMarkIcon } from './components/QuestionMarkIcon';

import styles from './CancelPremium.module.css';

export function CancelPremium(): JSX.Element | null {
  const {
    status,
    isSuccess,
    cancelStatus,
    handleCancelPremium,
    isCancelPremiumError,
    isCancelPremiumLoading,
  } = useCancelPremium();

  if (isCancelPremiumError) {
    return (
      <Error text="    ,    " />
    );
  }

  if (isCancelPremiumLoading) {
    return (
      <Loader className={styles.loader}>
         . , ...
      </Loader>
    );
  }

  if (isSuccess) {
    return (
      <div className={styles.success}>
        <h3>  !</h3>
        {cancelStatus === 'Canceled' ? (
          <p>       1-7 </p>
        ) : null}
        <Link to={RoutePath.Root} tabIndex={-1}>
          <Button
            className={styles.button}
            variant="primary"
            data-close="close-button"
          >
               
          </Button>
        </Link>
      </div>
    );
  }

  return (
    <div className={styles.wrapper}>
      <QuestionMarkIcon className={styles.question} />
      <div className={styles.info}>
        <h3 className={styles.title}>
          ,    
        </h3>
        <br />
        <span className={styles.text}>
              YouTube   1   ?
        </span>
      </div>
      <div className={styles.buttons}>
        <Button onClick={handleCancelPremium} variant="secondary">
          , 
        </Button>
        <Link to={RoutePath.Root}>
          <Button variant="primary" data-close="close-button">
             
          </Button>
        </Link>
      </div>
    </div>
  );
}
