import clsx from 'clsx';
import styles from './PaymentMethodItem.module.css';
import { PaymentMethod } from '@app/storage/session/shared';
import { SBPIcon } from './SBPIcon';
import { CardsIcons } from './CardsIcons';

type PaymentMethodProps = {
  method: PaymentMethod;
  active: boolean;
  onClick: (method: PaymentMethod) => void;
};

export const PaymentMethodItem = ({
  method,
  active,
  onClick,
}: PaymentMethodProps): JSX.Element => {
  const handleClick = () => {
    onClick(method);
  };

  return (
    <button
      className={clsx({
        [styles['payment-method-item']]: true,
        [styles.active]: active,
      })}
      onClick={handleClick}
    >
      {method === PaymentMethod.Sbp ? (
        <SBPIcon />
      ) : (
        <>
          <CardsIcons />
          <span> </span>
        </>
      )}
    </button>
  );
};
