import clsx from 'clsx';
import styles from './TariffItem.module.css';
import { TariffData } from '@api/premium/urls';

type TariffItemProps = {
  tariff: TariffData;
  onClick: (tariff: TariffData) => void;
  selected: boolean;
};

export const TariffItem = ({
  tariff,
  onClick,
  selected,
}: TariffItemProps): JSX.Element => {
  const { price, period } = tariff;
  const handleClick = () => {
    onClick(tariff);
  };

  return (
    <button
      className={clsx({
        [styles['tariff-item']]: true,
        [styles.active]: selected,
      })}
      onClick={handleClick}
    >
      {price}  / <span className={styles.period}>{period}</span>
    </button>
  );
};
