import {
  LocalStoragePremiumKey,
  setLocalStoragePremiumData,
} from '@app/storage/local/premium';
import { Loader } from '@content/components/PageLoader/ui/Loader';
import { Button } from '@popup/shared/components/Button';
import { useChangeDevice } from '@popup/shared/components/ChangeDeviceContent';
import { Error } from '@popup/shared/components/errors/Error';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { Prohibited } from '@popup/shared/icons/Prohibited';
import { BaseRoutePath } from '@popup/shared/routes/types/RoutePath';
import { useCallback, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '../../types/RoutePath';

import styles from './ChangeDevice.module.css';

export function ChangeDevice(): JSX.Element {
  const navigate = useNavigate();
  const {
    handleCancelChangeDevice,
    handleChangeDevice,
    isChangeDeviceError,
    isChangeDeviceLoading,
  } = useChangeDevice(BaseRoutePath.Root);
  const {
    handleClick: createUser,
    loading: isUserCreating,
    isSuccess,
  } = useHandlePremiumWithoutTelegram();

  useEffect(() => {
    if (!isSuccess) return;

    async function finalizeUserIdCreate() {
      await setLocalStoragePremiumData({
        [LocalStoragePremiumKey.IsChangeDeviceError]: false,
      });

      navigate(RoutePath.Tariffs);
    }

    finalizeUserIdCreate();
  }, [isSuccess, navigate]);

  const handleCreateUser = useCallback(() => {
    createUser();
  }, [createUser]);

  if (isUserCreating) {
    return <Loader className={styles.loader}>  ID...</Loader>;
  }

  if (isChangeDeviceLoading) {
    return <Loader className={styles.loader}> ...</Loader>;
  }

  if (isChangeDeviceError) {
    return <Error className={styles.error} />;
  }

  return (
    <div className={styles.wrapper}>
      <Prohibited className={styles.icon} />
      <div className={styles.content}>
        <p className={styles.title}>
           ID     
        </p>
        <p className={styles.description}>
            ID   
          <br />
             .
        </p>
      </div>
      <div className={styles.buttons}>
        <Button onClick={handleCreateUser} variant="primary">
              1  
        </Button>
        <Button onClick={handleChangeDevice} variant="secondary">
           ID 
        </Button>
        <Button
          onClick={handleCancelChangeDevice}
          variant="link"
          className={styles.link}
        >
          
        </Button>
      </div>
    </div>
  );
}
